.section--hero-home {
	@extend %section--hero;

	.row {
		display: flex;
		align-items: flex-end;
	}

	.background {
		&:before {
			background: #878887;
		}
	}

	.title {
		position: relative;
		margin: 0;
	}

	@include media-up(md) {

		margin-bottom: 280px;

		.container {
			padding-bottom: 0;
		}

		.picture {
			margin-bottom: -140px;
		}

		.group--cta {
			margin: 15px 0 70px 0;
		}

	}

	@include media-down(sm) {
        margin-bottom: 70px;

		.container {
			padding-bottom: 0;
		}

		.picture {
            // margin-bottom: -70px;
            margin-bottom: 35px;
        }

		.background {
			height: 100%;
		}

		.group--cta {
			margin: 0 0 70px 0;
        }
	}
}

.section--hero-home-v1 {
	@extend %section--hero;

	.background {
		&:before {
			background: #878887;
		}
	}

	.group--cta {
		margin: 60px 0 140px 0;
	}

	.title {
		position: relative;
	}

	@include media-down(sm) {

		.background {
			height: 132vw;
		}

		.group--cta {
			margin: 30px 0;
		}
	}
}

.section:not(:first-child) {
	opacity: 0;

	.ready & {
		opacity: 1;
	}
}

.control {
	position: relative;
	display: flex;
	margin-bottom: 15px;
	align-items: center;

	.control__input {
		appearance: none;
		border: none;
		box-shadow: none;

		width: percentage(3 / 5);

		padding: 30px 20px;

		font-size: 16px;
		font-weight: 300;
		line-height: 1;

		color: $foreground;
		background: #dedede;
	}

	.control__button {
		appearance: none;
		border: none;
		box-shadow: none;

		width: percentage(2 / 5);

		padding: 30px 20px;

		font-size: 16px;
		font-weight: 500;
		line-height: 1;

		color: $foreground;
		background: white;
	}

	@include media-down(sm) {
		flex-wrap: wrap;

		.control__input:not(.control__checkbox),
		.control__button {
			flex: 0 0 100%;
		}
	}
}

.control--checkbox {
	.control__input {
		width: 17px;
		height: 17px;
		padding: 0;
		margin-right: 10px;
		font-size: 0;
		color: $foreground;
		background: #C9C0B6;
	}

	.control__label {
		font-size: 11px;
		font-weight: 300;
		line-height: 1.2;
		color: $foreground;
	}
}

::placeholder {
	color: #878887;
}

.cover {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: #908e91;
	z-index: 1000;

	.inner {
		padding: 140px;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.picture {
		// position: absolute;
		max-width: 100%;
		max-height: 100%;

		img {
			width: auto;
			max-width: 100%;
			max-height: 100%;
		}
	}
}

.title,
.abstract,
.description {
	position: relative;
	z-index: 1;
}
