  0% {
    stroke-dasharray: 0 70; }
  45% {
    stroke-dashoffset: 0;
    stroke-dasharray: 70 70; }
  90% {
    stroke-dashoffset: -70;
    stroke-dasharray: 70 70; }
  100% {
    stroke-dashoffset: -70;
    stroke-dasharray: 70 70; } }

@keyframes stroke-color {
  0% {
